<template>
  <v-container
    id="login"
    class="fill-height justify-center"
    tag="section"
  >
    <v-row justify="center">
      <v-slide-y-transition appear>
        <base-material-card
          color="success"
          light
          max-width="100%"
          width="400"
          class="px-5 py-3"
        >
          <template v-slot:heading>
            <div class="text-center">
              <h1 class="display-2 font-weight-bold mb-2">
                Login
              </h1>

              <v-btn
                v-for="(social, i) in socials"
                :key="i"
                :href="social.href"
                class="ma-1"
                icon
                rel="noopener"
                target="_blank"
              >
                <v-icon
                  v-text="social.icon"
                />
              </v-btn>
            </div>
          </template>

          <v-card-text class="text-center">
            <div class="text-center grey--text body-1 font-weight-light">
              Or Be Classical
            </div>

            <v-text-field
              color="secondary"
              label="First Name..."
              prepend-icon="mdi-face"
              class="mt-10"
              v-model="userInfo.username"
            />

            <v-text-field
              class="mb-8"
              color="secondary"
              label="Password..."
              prepend-icon="mdi-lock-outline"
              v-model="userInfo.password"
              type="password"
            />

            <pages-btn
              large
              color=""
              depressed
              class="v-btn--text success--text"
              @click="doLogin"
            >
              登 陆
            </pages-btn>
          </v-card-text>
        </base-material-card>
      </v-slide-y-transition>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'PagesLogin',

    components: {
      PagesBtn: () => import('./Btn'),
    },

    methods: {
      doLogin() {
        if (!this.userInfo.username) {
          alert('用户名不能为空');
          return false;
        }

        if (!this.userInfo.password) {
          alert('密码不能为空');
          return false;
        }

        let postData = { username: this.userInfo.username, password: this.userInfo.password};
        this.$http.post('/user/login', postData)
        .then(response => {
          if (response.success) {
            let access_token = response.data.access_token;
            this.$store.commit('SET_TOKEN', access_token)
            this.$router.push({path:'/'});
          } else {
            alert(response.msg);
          }
        })
        .catch(error =>{
          console.log(error);
        })
      }
    },

    data: () => ({

      userInfo: {
        username: 'admin',
        password: '111222'
      },

      socials: [
        {
          href: '#',
          icon: 'mdi-facebook-box',
        },
        {
          href: '#',
          icon: 'mdi-twitter',
        },
        {
          href: '#',
          icon: 'mdi-github-box',
        },
      ],
    }),
  }
</script>
